<template>
    <div class="homePage">
        <div class="homePageName">
            E-API
        </div>
        <div class="homePageTitle">
            API文档、API调试、API Mock、浏览器(可跨域)
        </div>
        <div class="homePageDescribe">
            API接口采用axios直接调用(使用时请符合前端axios接口调用规范)
        </div>
        <div class="homePageDescribe">
            浏览器取消跨域、调起摄像头必须为https或本地(localhost)等的限制
        </div>
        <div class="homePageDownload">
            <el-dropdown split-button type="primary" 
                icon="el-icon-download" @click="goDownload" 
                @command="handleDownload"
                style="margin-right: 10px;">
                下载桌面版
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item command="x64">下载windows桌面版(64位)</el-dropdown-item>
                    <el-dropdown-item command="x86">下载windows桌面版(34位)</el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
            <el-button type="danger" icon="el-icon-download" @click="goChrome">Chrome插件版</el-button>
            <el-button type="danger" plain icon="el-icon-s-platform" @click="goWeb">web版本(不能调试接口)</el-button>
        </div>
        <div class="homePageSwipt">
            <el-carousel height="800px" class="homePageSwiptItem">
                <el-carousel-item v-for="(item,index) in swiptList" :key="index">
                    <img :src="item" class="homePageSwiptItem" />
                </el-carousel-item>
            </el-carousel>
        </div>
    </div>
</template>

<script>
import s1 from '../../assets/image/s1.png'
import s2 from '../../assets/image/s2.png'
import s3 from '../../assets/image/s3.png'
import s4 from '../../assets/image/s4.png'
import s5 from '../../assets/image/s5.png'
import s6 from '../../assets/image/s6.png'
export default {
    data(){
        return {
            swiptList:[s1,s2,s3,s4,s5,s6]
        }
    },
    methods: {
        goDownload(){
            var agent = navigator.userAgent.toLowerCase();
            var isMac = /macintosh|mac os x/i.test(navigator.userAgent);
            if (agent.indexOf("win32") >= 0 || agent.indexOf("wow32") >= 0) {
                window.open('http://img.nodebook.top/e-api/E-API-86.exe')
            }
            if (agent.indexOf("win64") >= 0 || agent.indexOf("wow64") >= 0) {
                window.open('http://img.nodebook.top/e-api/E-API-x64.exe')
            }
            if (isMac) {
                alert('正在开发mac版本，敬请期待')
            }
        },
        handleDownload(command){
            switch(command){
                case 'x64':
                    window.open('http://img.nodebook.top/e-api/E-API-x64.exe')
                break;
                case 'x86':
                    window.open('http://img.nodebook.top/e-api/E-API-86.exe')
                break;
            }
        },
        goChrome(){
            window.open('http://img.nodebook.top/e-api/e-api.zip')
        },
        goWeb(){
            window.open('http://e-api-egg.nodebook.top/release/web')
        }
    }
}
</script>

<style scoped>
.homePage{
    width: 100%;
    height: auto;
    padding-top: 50px;
    padding-bottom: 100px;
    box-sizing: border-box;
}
.homePageName{
    width: 100%;
    height: 100px;
    /* background: yellowgreen; */
    text-align: center;
    line-height: 100px;
    font-size: 80px;
}
.homePageTitle{
    width: 100%;
    height: 70px;
    /* background: yellowgreen; */
    text-align: center;
    line-height: 70px;
    font-size: 30px;
    color: #262626;
}
.homePageDescribe{
    width: 100%;
    height: 35px;
    /* background: yellowgreen; */
    text-align: center;
    line-height: 35px;
    color: #5C5D5E;
    font-size: 14px;
}
.homePageDownload{
    width: 100%;
    height: 80px;
    /* background: yellowgreen; */
    display: flex;
    justify-content: center;
    align-items: center;
}
.homePageSwipt{
    width: 100%;
    height: 800px;
    display: flex;
    justify-content: center;
    
}
.homePageSwiptBox{
    width: 1200px;
    height: 100%;
    margin: 0 auto;
    border: 1px solid #262626;
}
.homePageSwiptItem{
    width: 1200px;
    height: 800px;
}
</style>